@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR&display=swap");

.work-container {
  margin-top: 10vw;
}
.room-for-title {
  display: flex;
  justify-content: center;
}

.works-title {
  font-size: 4vw;
  text-decoration: underline;
  font-weight: bold;
}
.work {
  margin: 2vw 2vw;
  width: 20vw;
  height: 25vw;
}
.room-for-worklist {
  display: grid;
  grid-template-columns: auto auto;
  align-items: center;
  justify-content: center;
}
.room-for-hook {
  width: 100%;
  height: 2px;
}
.work__img {
  border-radius: 10%;
  width: 100%;
  height: 80%;
}
.work__text {
  margin: 0;
  font-size: 2vw;
  display: flex;
  justify-content: center;
  cursor: pointer;
}

.openpage {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
  background-color: rgba(255, 255, 255, 0.8);
}
.close-btn {
  position: fixed;
  margin: 5vw;
  top: 0;
  right: 0;
}
.title-box {
  display: flex;
  justify-content: center;
  margin-top: 10vh;
}
.openpage-title {
  font-weight: bold;
  font-size: 4vw;
  text-decoration: underline;
}

.openpage-content-box {
  display: flex;
  justify-content: center;
}
.openpage-img-container {
  margin-right: 2vh;
  height: 60vh;
  width: 60vh;
}
.openpage-img {
  width: 100%;
  height: 100%;
}

.openpage-intro-box {
  margin-left: 2vh;
  width: 45vh;
  height: 60vh;
}
.openpage-intro-list {
  list-style: none;
  font-family: "Noto Sans KR", sans-serif;
  font-size: 20px;
  font-weight: bold;
}
.link-box {
  display: flex;
  justify-content: center;
  margin-top: 30vh;
}
.link {
  font-size: 2vw;
  font-family: "Noto Sans KR", sans-serif;
  font-weight: bold;
  cursor: pointer;
}

@media screen and (max-width: 550px) {
  .openpage-intro-list {
    font-size: 12px;
    font-weight: bold;
  }
  .openpage-intro-list-box {
    padding-left: 0;
  }
  .openpage-img {
    width: 80%;
    height: 80%;
    margin-left: 5vw;
  }
}
