.contact-container {
  margin-top: 10vw;
  display: flex;
  justify-content: center;
}
.img-container {
  width: 50%;
  height: 20%;
  border: 1px solid black;
}
.contact-text {
  width: 30vw;
  height: 36vw;
  margin-right: 5vw;
  display: flex;
  flex-direction: column;
}
.contact-title {
  font-size: 4vw;
  font-weight: bold;
  text-decoration: underline;
}
.contact-ids {
  font-size: 1.5vw;
}
.contact-img {
  width: 25vw;
  height: 30vw;
  border-radius: 10%;
}
.li {
  list-style: none;
}
.icon {
  width: 3vw;
  height: 3vw;
}

@media screen and (max-width: 550px) {
  .contact-container {
    height: 70vw;
  }
  .contact-text {
    width: 40%;
    height: 100%;
  }
  .contact-ids {
    padding: 0;
  }
  .contact-ids li {
    font-size: 4vw;
  }
  .contact-img {
    width: 45%;
    height: 100%;
  }
}
