@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300&display=swap");

body {
  font-family: "Roboto Mono", monospace;
}
.simple-intro {
  display: flex;
  justify-content: center;
}
.simple-intro-text {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.simple-intro-name {
  font-size: 5vw;
}
.simple-intro-job {
  font-size: 3vw;
}
.simple-intro-img {
  width: 15vw;
  height: 15vw;
  border-radius: 100%;
}

.intro-container {
  display: flex;
  justify-content: center;
}
.photo1 {
  width: 30vw;
  height: 40vw;
  border-radius: 10%;
}
.text-container {
  text-align: center;
  justify-content: center;
  align-items: center;
  width: 40vw;
  height: 5vw;
  border: 1px solid black;
}

.bio-container {
  margin-top: 5vw;
  display: flex;
  justify-content: center;
}
.bio-text {
  width: 30vw;
  height: 36vw;
  margin-left: 5vw;
  display: flex;
  flex-direction: column;
}
.bio-title {
  font-size: 4vw;
  font-weight: bold;
  text-decoration: underline;
}
.bio-p {
  font-size: 1.5vw;
}
.bio-img {
  width: 25vw;
  height: 30vw;
  border-radius: 10%;
}
@media screen and (max-width: 550px) {
  .intro-container {
  }
  .simple-intro {
    display: block;
    text-align: center;
  }
  .simple-intro-name {
    font-size: 10vw;
  }
  .simple-intro-job {
    font-size: 5vw;
  }
  .simple-intro-img {
    width: 20vw;
    height: 20vw;
  }
  .bio-container {
    height: 70vw;
  }
  .bio-img {
    margin-left: -12vw;
    margin-right: 6vw;
    width: 48vw;
    height: 100%;
  }
  .bio-text {
    margin-left: 0;
    height: 100%;
  }
  .bio-p li {
    margin-left: -10vw;
    font-size: 3vw;
  }
}
